module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-collapse-custom',\n  template: `\n    <nz-collapse [nzBordered]=\"false\">\n      <nz-collapse-panel *ngFor=\"let panel of panels\" [nzHeader]=\"panel.name\" [nzActive]=\"panel.active\"\n        [ngStyle]=\"panel.customStyle\">\n        <p>{{panel.name}} content</p>\n      </nz-collapse-panel>\n    </nz-collapse>\n  `,\n  styles  : []\n})\nexport class NzDemoCollapseCustomComponent {\n  panels = [\n    {\n      active     : true,\n      disabled   : false,\n      name       : 'This is panel header 1',\n      customStyle: {\n        'background'   : '#f7f7f7',\n        'border-radius': '4px',\n        'margin-bottom': '24px',\n        'border'       : '0px'\n      }\n    },\n    {\n      active     : false,\n      disabled   : true,\n      name       : 'This is panel header 2',\n      customStyle: {\n        'background'   : '#f7f7f7',\n        'border-radius': '4px',\n        'margin-bottom': '24px',\n        'border'       : '0px'\n      }\n    },\n    {\n      active     : false,\n      disabled   : false,\n      name       : 'This is panel header 3',\n      customStyle: {\n        'background'   : '#f7f7f7',\n        'border-radius': '4px',\n        'margin-bottom': '24px',\n        'border'       : '0px'\n      }\n    }\n  ];\n}\n"