module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-collapse-mix',\n  template: `\n    <nz-collapse>\n      <nz-collapse-panel *ngFor=\"let panel of panels\" [nzHeader]=\"panel.name\" [nzActive]=\"panel.active\">\n        <p>{{panel.name}}</p>\n        <div *ngIf=\"panel.childPanel&&panel.childPanel.length>0\">\n          <nz-collapse>\n            <nz-collapse-panel *ngFor=\"let childPanel of panel.childPanel\" [nzHeader]=\"childPanel.name\"\n              [nzActive]=\"childPanel.active\">\n              <p>A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.</p>\n            </nz-collapse-panel>\n          </nz-collapse>\n        </div>\n      </nz-collapse-panel>\n    </nz-collapse>\n  `,\n  styles  : []\n})\nexport class NzDemoCollapseMixComponent {\n  panels = [\n    {\n      active    : true,\n      disabled  : false,\n      name      : 'This is panel header 1',\n      childPanel: [\n        {\n          active: true,\n          name  : 'This is panel header 1-1'\n        },\n        {\n          active: false,\n          name  : 'This is panel header 1-2'\n        }\n      ]\n    },\n    {\n      active  : false,\n      disabled: true,\n      name    : 'This is panel header 2'\n    },\n    {\n      active  : false,\n      disabled: false,\n      name    : 'This is panel header 3'\n    }\n  ];\n}\n"